/**
 *  modalFormOk refmodalForm
 *  superQuery  refsuperQueryModal
 * dataurl list  delete  deleteBatch
 */
import { filterObj } from '@/utils/util';
import { postAction } from '@/api/manage'
import { mapState } from 'vuex'
import { Notify } from 'vant';

export const GpayListMixin = {
	data() {
		return {
			//token header
			// tokenHeader: {'X-Access-Token': Vue.ls.get(ACCESS_TOKEN)},
			/* -queryParam */
			//result
			resultName: '',
			queryParam: {},
			/*  */
			dataSource: [],
			/*  */
			ipagination: {
				current: 0,
				pageSize: 10,
				// pageSizeOptions: ['10', '20', '30', '50', '100'],//
				showTotal: (total, range) => {
					return range[0] + "-" + range[1] + " " + total + ""
				},
				showQuickJumper: true,
				showSizeChanger: true,
				total: 0
			},
			/*  */
			isorter: {
				column: 'iCreateTime',
				order: 'desc',
			},
			/*  */
			filters: {},
			refreshing: false,
			/* table */
			loading: false,
			finished: false,
			/*  */
			loadedall: false,
			/* tablekeys*/
			selectedRowKeys: [],
			/* tablerecords*/
			selectionRows: [],
			/*  */
			toggleSearchStatus: false,
			/*  */
			superQueryFlag: false,
			/*  */
			superQueryParams: '',
			/**  */
			superQueryMatchType: 'and',
			/**  */
			error: false,
			/**  */
			dataEmpty: false
		}
	},
	computed: {
		...mapState(["isLoading"]),
	},
	created() {
		if (!this.disableMixinCreated) {
			console.log('')
		}
	},
	methods: {
		loadData() {
			if (!this.url.list) {
				this.$toast.fail("url.list!")
				return
			}
			if (this.loadedall) {
				console.log('')
				return
			}
			// 1
			this.ipagination.current = this.ipagination.current + 1;
			console.log('loadDataloadData')
			let params = this.getQueryParams();//
			postAction(this.url.list, params).then((res) => {
				if (res.iRspCode === 0) {
					console.log('loadDataloadData------1')
					let data = res.data[this.resultName]
					if (data && data.length > 0) {
						console.log('loadDataloadData------2')

						this.dataSource = this.dataSource.concat(res.data[this.resultName])

						// 
						if (this.dataSource.length < this.ipagination.pageSize) {
							console.log('loadDataloadData------5---')
							this.loadedall = true;
						}
					} else {
						console.log('loadDataloadData------5---')
						this.loadedall = true;
					}
				} else {
					this.error = false
					//this.$toast.fail(res.message)
				}
			}).catch(err => {
				console.log(err)
				this.error = false
				this.loading = false;
				this.refreshing = false;
				this.loadedall = true;
			}).finally(() => {
				this.loading = false;
				this.refreshing = false;
				console.log('finally')
			})
		},
		refreshData() {
			if(window.networkFlag&&window.networkFlag==2){
				this.loadedall = true; 
				this.refreshing = false; 
				this.loading = false;
				//Notify({ type: 'warning', message: '' });
				return
			}
			this.ipagination.current = 0;
			// 
			this.loadedall = false;
			this.dataSource = []
			// 
			//  loading  true
			this.loading = true;
			this.refreshing = false
			this.loadData();
		},
		initParam() { },
		getQueryParams() {
			//
			let sqp = {}
			if (this.superQueryParams) {
				sqp['superQueryParams'] = encodeURI(this.superQueryParams)
				sqp['superQueryMatchType'] = this.superQueryMatchType
			}
			let param = Object.assign(sqp, this.queryParam, this.isorter, this.filters);
			param.iPageNo = this.ipagination.current - 1;
			param.iPageSize = this.ipagination.pageSize;
			param.sOrder = param.order;
			param.sColumn = param.column;
			delete param.order
			delete param.column
			return filterObj(param);
		},
	}
}
