<template>
  <div class="pay-list" style="padding-top: 1.2rem;padding-bottom: 0.4rem">
    <div v-if="step===1">
      <van-nav-bar
          title=""
          left-text=""
          class="header_title"
          @click-left="$router.back(-1);"
          safe-area-inset-top
          placeholder
          left-arrow
          :fixed="true"
      />
      <div>
        <van-field
            v-model="sSenderNo"
            label=""
            type="number"
            placeholder=" 95555"
            show-word-limit
            required
        />

        <van-field
            v-model="message"
            rows="3"
            autosize
            label=""
            type="textarea"
            maxlength="140"
            placeholder=""
            show-word-limit
            required
        />
        <div style="margin-top: 0.4rem">
          <van-button
              type="info"
              size="mini"
              style="width:50%;margin:0 auto;padding:15px 0"
              round
              block
              :disabled="analyseDisabled"
              @click="analyseSMS">
            
          </van-button>
        </div>
        <template v-if="analysed">
          <van-cell-group title="">
            <van-cell :title="bankName" value="" />
            <!--          <van-cell :title="amount" value="" required is-link />-->
            <!--          <van-cell :title="allAmount" value="" required is-link />-->
            <van-cell v-for="(item,index) in numbers" :title="item.value"  required :value="item.type === 'none' ? '' : item.type" :key="index" is-link @click="showPicker = true;pickerIndex = index"/>
          </van-cell-group>
          <div style="margin-top:0.4rem;margin-bottom: 0.4rem">
            <van-button
                :loading="loading"
                loading-text="..."
                type="default"
                size="mini"
                style="width:50%;margin:0 auto;padding:15px 0"
                round
                block
                @click="nextStep"
                :disabled="disableNextStep"
            >
               
            </van-button>
          </div>
          <van-popup v-model="showPicker" round position="bottom">
            <van-picker
                v-if="showPicker"
                title=""
                show-toolbar
                :default-index = 'defaultIndex'
                :columns="selOptions"
                @cancel="showPicker = false"
                @confirm="onConfirm"
            />
          </van-popup>
        </template>
      </div>
    </div>
    <div v-if="step===2">
      <van-nav-bar
          title=""
          class="header_title"
          @click-left="step =1"
          safe-area-inset-top
          placeholder
          left-arrow
          :fixed="true"
      />
      <div>
        <van-cell title=""  :value="sSenderNo" />
        <van-cell title=""  :label="sTemplate" />
        <div style="padding:10px 16px;color: #969799;font-size: 12px">
          <br/>
          <br/>
          <br/>
          ...<br/>
          ...<br/>
          <div v-for="(log,index) in testLogs" :key="index">
            {{log}}
          </div>
        </div>
<!--        <van-button @click="test"></van-button>-->
      </div>
      <van-dialog
          v-model="showPopup"
          title=""
          theme="round"
          show-cancel-button
          confirmButtonText = ''
          cancelButtonText = ''
          :before-close = "addSMSTp"
      >
        <van-cell v-for="(item,index) in testNumbers" :title="item.type"  :value="item.value" :key="index"/>
      </van-dialog>
    </div>
  </div>
</template>

<script>
import {addUserSmsTpl,} from '@/api/otc'
export default {
name: "addBankCard",
  data(){
    return {
      step:1,
      message:'',
      sSenderNo:null,
      sTemplate:'',
      showPicker:false,
      loading:false,
      numbers:[],
      bankName:'',
      amount:null,
      allAmount:null,
      pickerIndex:null,
      showPopup:false,
      selOptions:[
        {type:'balance',text:''},
        {type:'amount',text:''},
        {type:'account',text:''},
        {type:'year',text:''},
        {type:'month',text:''},
        {type:'day',text:''},
        {type:'hour',text:''},
        {type:'minute',text:''},
        {type:'other',text:''},
      ],
      testText:[
          '==1234=1222.1=332195574',
          '325104812344848.1138383',
          '257803121.00,27498.71'
      ],
      testIndex:2,
      analysed:false,
      testLogs:[],
      testNumbers:[]
    }
  },
  computed:{
    analyseDisabled(){
      return this.message === '' || !this.sSenderNo
    },
    defaultIndex(){
      const index = this.selOptions.findIndex(i=>i.text === this.numbers[this.pickerIndex].type)
      return  index < 0 ? 0 : index
    },
    disableNextStep(){
      let disabled = false
      this.numbers.forEach(item=>{
        if(item.type=='none') disabled = true
      })
      return disabled
    }
  },
  created(){
    document.addEventListener('onSMSArrive',(e)=>{
      // debugger
      if(this.step ===2){
        let { address , body } = e.data
        let sms = body
        //
        if(address != this.sSenderNo) return
        this.testLogs.push(''+ this.sSenderNo +'')
        //
        let idx1 = sms.indexOf('')
        let idx2 = sms.indexOf('')
        if(idx1 === -1 || idx2 === -1){
          idx1 = sms.indexOf('[')
          idx2 = sms.indexOf(']')
        }
        if(idx1 === -1 || idx2 === -1) {
          this.testLogs.push('')
          return
        }
        this.testLogs.push(':'+sms.substr(idx1+1,idx2-idx1-1))
        if(this.bankName != sms.substr(idx1+1,idx2-idx1-1)) {
          this.testLogs.push('...')
          return
        }
        this.testLogs.push('' + sms)
        let str = sms.replace(/,/g,'')
        const tmpNumbers = []
        sms.replace(/[0-9]*\.?[0-9]+/g, ( x )=>{
          tmpNumbers.push({
            value:x,
            type:''
          })
        })
        const typeStrArr = []
        tmpNumbers.forEach((item,index)=>{
          this.sTemplate.replace(/(?<=\{\{)[^}]*(?=\}\})/g,(x)=>{
            typeStrArr.push(x)
          })
          if(index<typeStrArr.length){
            str = str.replace(item.value,'{{'+typeStrArr[index]+'}}')
            item.type = typeStrArr[index]
          }
        })
        // debugger
        if(this.matchStrings(this.sTemplate,str)){
          tmpNumbers.forEach(item=>{
            this.testLogs.push(item.type + ':' + item.value)
          })
          this.testNumbers = tmpNumbers
          this.showPopup = true
          this.testLogs.push('...')
        }else{
          this.testLogs.push('...')
        }
      }
    })
  },
  beforeDestroy(){
    document.removeEventListener('onSMSArrive')
  },
  methods:{
    test(){
      console.log('!')
      const event = document.createEvent("HTMLEvents")
      event.data={}
      event.data.address = this.sSenderNo
      event.data.body = this.testText[this.testIndex]
      event.initEvent("onSMSArrive", false, false);
      document.dispatchEvent(event);
    },
    matchStrings(value1,value2){

      // debugger
      let str1 = value1
      let str3 = value1
      let str2 = value2
      let str4 = value2

      const totalLength = str1.length + str2.length

      let a = str1.substr(0,1)
      let b = str2.substr(0,1)
      str1 = str1.substr(1)
      str2 = str2.substr(1)
      let z = 0
      let x = 0
      while (a !== undefined && a !=='' && b !== undefined &&  b !=='' &&  a === b) {
          z++
          a = str1.substr(0,1)
          b = str2.substr(0,1)
          str1 = str1.substr(1)
          str2 = str2.substr(1)
      }

      // debugger
      let c = str3.substr(str3.length-1,1)
      let d = str4.substr(str3.length-1,1)
      str3 = str3.substr(0,str3.length-1)
      str4 = str4.substr(0,str4.length-1)
      while ( c !== undefined && c !=='' && d !== undefined && d !=='' && c === d) {
        x++
        c = str3.substr(str3.length-1,1)
        d = str4.substr(str3.length-1,1)
        str3 = str3.substr(0,str3.length-1)
        str4 = str4.substr(0,str4.length-1)
      }

      // debugger
      const e = (x+z)/totalLength
      return e > 0.6
    },
    addSMSTp(action,done){
      // this.loading = true
      if(action ==='cancel'){
        done()
        return
      }
      // debugger
      const params = {
        sBankName:this.bankName,
        sSenderNo:this.sSenderNo,
        sTemplate:this.sTemplate
      }
      addUserSmsTpl(params)
      .then((res)=>{
        // debugger
        if(res.iRspCode === 0){
          this.$message.success('')
          // this.loading = false
          done()
          this.$router.replace('/smsModels')
        }else {
          this.$message.error(',')
          done(false)
        }

      })
    },
    nextStep(){
      let str = this.message.replace(/,/g,'')
      let num1 = 0
      let num2 = 0
      let num3 = 0
      this.numbers.forEach(item=>{
        str = str.replace(item.value,'{{'+item.type + "}}")
        if(item.type == '') num1 +=1
        if(item.type == '') num2 +=1
        if(item.type == '') num3 +=1
      })
      if(num1 === 0){
        this.$message.error('')
        return
      }
      if(num1 > 1){
        this.$message.error('')
        return
      }
      if(num2 > 1){
        this.$message.error('')
        return
      }
      if(num3 > 1){
        this.$message.error('')
        return
      }
      this.sTemplate = str
      this.testLogs = []
      this.step = 2

    },
    analyseSMS(){
      // debugger
      this.analysed = false
      let str = this.message.replace(/,/g,'')
      this.numbers = []
      let idx1 = str.indexOf('')
      let idx2 = str.indexOf('')
      if(idx1 === -1 || idx2 === -1){
        idx1 = str.indexOf('[')
        idx2 = str.indexOf(']')
      }
      if(idx1 === -1 || idx2 === -1){
        this.$message.error('')
        return
      }else{
        this.bankName = str.substr(idx1+1,idx2-idx1-1)
      }
      let numbers = [];
      const tmpAmounts = []
      str.replace(/[0-9]*\.?[0-9]+/g, ( x )=>{
        // debugger
        if(x.includes('.')) {

          tmpAmounts.push(x)
        }else{

          numbers.push(x)
        }
      })
      if(tmpAmounts.length === 2){
        tmpAmounts.sort((a,b)=> Number(a) - Number(b))
          this.numbers.push({
            type:'',
            value:tmpAmounts[0],
          })
          this.numbers.push({
            type:'',
            value:tmpAmounts[1],
          })

      }else {
        numbers = tmpAmounts.concat(numbers)
      }
      const tmp = []
      numbers.forEach(item=>{
        // debugger
        const index = str.indexOf(item)
        switch (str.slice(index+item.length,index+item.length+1)){
          case '':
            this.numbers.push({
              type:'',
              value:item,
            })
                break
          case '':
            this.numbers.push({
              type:'',
              value:item,
            })
            break
          case '':
            this.numbers.push({
              type:'',
              value:item,
            })
            break
          case '':
            this.numbers.push({
              type:'',
              value:item,
            })
            break
          case '':
            this.numbers.push({
              type:'',
              value:item,
            })
            break
          case ':':
            this.numbers.push({
              type:'',
              value:item,
            })
            break
          default:
            this.numbers.push({
              type:'none',
              value:item,
            })
        }
        if(str.slice(index-1,index) ===':') {
          this.numbers[this.numbers.length - 1].type=''
        }
        if(str.slice(index-4,index).includes('')||str.slice(index-4,index).includes('')) {
          this.numbers[this.numbers.length - 1].type=''
        }
        if(tmpAmounts.length !== 2 && str.slice(index+item.length,index+item.length+1) ==='') {
          tmp.push(item)
        }
        const aaa = this.numbers
        if(tmp.length === 2){
          // debugger
          tmp.sort((a,b)=> Number(a) - Number(b))
          this.numbers.find(i=>i.value==tmp[0]).type =''
          this.numbers.find(i=>i.value==tmp[1]).type =''
        }
        console.log(item)
        console.log(str.slice(index+item.length,index+item.length+1))
        console.log(str.slice(index-2,index))
      })

      this.analysed = true
    },
    onConfirm(value){
      this.numbers[this.pickerIndex].type = value.text
      this.showPicker = false
    },

  }
}
</script>

<style lang="less" scoped>
/deep/ .van-nav-bar{
  background: #3D7EFF;
  .van-nav-bar__title {
    color:white;
  }
}
</style>
