<template>
    <div>
        <router-view/>
<!--        <van-nav-bar-->
<!--            title=""-->
<!--            left-text=""-->
<!--            class="header_title"-->
<!--            @click-left="onClickLeft"-->
<!--            safe-area-inset-top-->
<!--            placeholder-->
<!--            left-arrow-->
<!--            :fixed="fixed"-->
<!--        />-->
        <div class="user-conts">
            <van-cell-group>
                <van-cell title="" :value="cellphone()| phone">
                </van-cell>
                <van-cell v-if="isHasEmail()" title="" :value="email()|email" isLink @click="setEmail(10)">
                </van-cell>
                <van-cell v-else title="" isLink>
                    <template #right-icon>
                        <a class="link-color" @click="setEmail(7)"></a>
                    </template>
                </van-cell>
                <van-cell title="" value="" is-link @click="goEditPsw"></van-cell>
                <van-cell title="" :value="iDrawalStatus() === 1 ? '' : ''" is-link @click="goEditDrawaPsw"></van-cell>
                <van-cell title="" value="" is-link @click="goPayList"></van-cell>
<!--                <van-cell v-if="iCertificateStatus() === 3 " title="" value="()" is-link @click="goAuthCode">-->
<!--                </van-cell>-->
<!--                <van-cell v-if="iCertificateStatus() === 0" title="">-->
<!--                    <template #right-icon>-->
<!--                        <a class="link-color" @click="goAuthCode"></a>-->
<!--                    </template>-->
<!--                </van-cell>-->
<!--                <van-cell v-if="iCertificateStatus() === 1" title="" value="" is-link @click="goAuthSuccess">-->
<!--                </van-cell>-->
<!--                 &lt;!&ndash; is-link @click="goAuth" &ndash;&gt;-->
<!--                <van-cell v-if="iCertificateStatus() === 2" title="">-->
<!--                    <template #right-icon>-->
<!--                        <div class="pass-style"><van-icon name="passed" /></div>-->
<!--                    </template>-->
<!--                </van-cell>-->

<!--                <van-cell v-if="iCertificateStatus() === 2&&(iLiveCertificateStatus() === 0 || iLiveCertificateStatus() === 3)" title="">-->
<!--                    <template #right-icon>-->
<!--                        <a class="link-color" @click="goLiveAuth"></a>-->
<!--                    </template>-->
<!--                </van-cell>-->
<!--                <van-cell v-if="iCertificateStatus() === 2&&(iLiveCertificateStatus() === 1)" title="" value="" is-link  @click="goLiveAuth">-->
<!--                </van-cell>-->
<!--                  &lt;!&ndash; value="" is-link @click="goLiveAuth" &ndash;&gt;-->
<!--                <van-cell v-if="iCertificateStatus() === 2&&(iLiveCertificateStatus() === 2)" title="">-->
<!--                    <template #right-icon>-->
<!--                        <div class="pass-style"><van-icon name="passed" /></div>-->
<!--                    </template>-->
<!--                </van-cell>-->
<!--                <van-cell v-if="iLiveCertificateStatus() === 2&&(iAdsStatus() === 0 ||iAdsStatus() === 3)" title="">-->
<!--                    <template #right-icon>-->
<!--                        <a class="link-color" @click="goAdsAuth"></a>-->
<!--                    </template>-->
<!--                </van-cell>-->
              <van-cell v-if="iAdsStatus() === 3 " title="" value="()" is-link @click="goAdsAuth"></van-cell>
                <van-cell v-if="iAdsStatus() === 0 " title="">
                    <template #right-icon>
                        <a class="link-color" @click="goAdsAuth"></a>
                    </template>
                </van-cell>
                <van-cell v-if="iAdsStatus() === 1" title="" value="">
                </van-cell>
                <!-- is-link @click="goAdsAuth" -->
                <van-cell v-if="iAdsStatus() === 2" title="">
                    <template #right-icon>
                        <div class="pass-style"><van-icon name="passed" /></div>
                    </template>
                </van-cell>

            </van-cell-group>
        </div>
        <!-- <van-dialog
                v-model="show"
                title=""
                show-cancel-button
                :beforeClose="closeDialog"
        >
            <van-field v-model="message" :label="label" placeholder=""/>
        </van-dialog> -->
        <!---->
        <van-popup v-model="show"
            :closeable="false"
            round
            title=""
            :close-on-click-overlay ="false"
            close-icon-position="top-right"
            position="bottom"
            :style="{ height: '35%', }"
        >   
            <div class="popup-title-hed"></div>
            <van-field
                class="nk-icon"
                v-model="message"
                center
                clearable
                label=""
                type="text"
                :maxlength="10"
                :left-icon="require('../../assets/img/nk-icon.png')"
                placeholder=""
            >
            </van-field>
            <div style="margin: 36px 16px; 0 16px">
                <van-button style="width:47%;display: inline-block;background: #FFFFFF;border: 1px solid #C8CBCE;color: #919DAF;" round block type="info" @click="closes">
                    
                </van-button>
                <van-button style="width:47%;display: inline-block; margin-left:.4rem;" round block type="info" @click="closeDialog">
                    
                </van-button>
            </div>
        </van-popup>

        <!--  -->
        <van-dialog v-model="showPsw" title="" confirmButtonColor="#3D7EFF" show-cancel-button :beforeClose="editPsw">
            <van-field
                v-model="password"
                center
                clearable
                required
                :maxlength="20"
                type="password"
                label=""
                placeholder=""
            >
            </van-field>
            <van-field
                v-model="newpassword"
                center
                clearable
                required
                :maxlength="20"
                type="password"
                label=""
                placeholder=""
            >
            </van-field>
            <van-field
                v-model="repassword"
                center
                clearable
                required
                :maxlength="20"
                type="password"
                label=""
                placeholder=""
            >
            </van-field>
        </van-dialog>
        <!--  -->
        <van-dialog v-model="drawalShow" :title="(this.iDrawalStatus() === 1 ? '' : '') +''" confirmButtonColor="#3D7EFF" show-cancel-button :beforeClose="drawalPsw">
            <van-field
                v-model="drawal.sDrawalPassword"
                center
                clearable
                required
                :maxlength="20"
                type="password"
                label=""
                placeholder=""
            >
            </van-field>
            <van-field
                v-model="drawal.sDpassword"
                center
                clearable
                required
                :maxlength="20"
                type="password"
                label=""
                placeholder=""
            >
            </van-field>
        </van-dialog>
        <email-modal ref="emailModal"/>
        <!---->
        <safe-verify-modal
            ref="safeVerifyModal"
            :iVerifyBusi='iVerifyBusi'
            @ok="applyVerify"
        />
        <!---->
        <van-action-sheet
            v-model="goAuthShow"
            :actions="actions"
            cancel-text=""
            close-on-click-action
            :close-on-click-overlay="false"
            @select="goAuth"
            @cancel="goAuthShow=false"
        />
    </div>
</template>

<script>
    import {Toast} from 'vant';
    import {mapGetters, mapActions} from 'vuex'
    import {changePassword, emailVerify, sendEmailVerifyCode} from '@/api/auth'
    import {resetDrawalPassword} from '@/api/udc'
    import SafeVerifyModal from "@/components/SafeVerifyModal/index";
    import EmailModal from "@/views/my/user_setting_email";
    import {regPwd} from "@/utils/validate"
    import {getAdsConfigList, robShareOrder} from "@/api/otc";

    export default {
        name: 'user-setting',
        components: {
            [Toast.name]: Toast,
            SafeVerifyModal,
            EmailModal,
        },
        data() {
            return {
                fixed: true,
                message: '',
                show: false,
                showPsw: false,
                showEmail: false,
                getEmailCodeFlag: true,
                label: '',
                password: '',
                emailCode: '',
                iVerifyBusi: 3,
                newpassword: '',
                repassword: '',
                action: null,
                emailCountTime: 60,
                goAuthShow: false,
                actions: [{ name: '' }, { name: '' }],
                drawalShow: false,
                drawal:{
                    sDrawalPassword: '',
                    sDpassword: ''
                },
                pwdType: '' // 
            }
        },
        mounted() {

        },
        created() {
            
        },
        methods: {
            ...mapGetters(['iDrawalStatus','iAdsStatus','iLiveCertificateStatus','iCertificateStatus', 'isHasEmail', 'isLogin', 'cellphone', 'email', 'userName', 'iIsChangeNameStatus', 'switchEmailVerify', 'switchCellphoneVerify', 'switchGoogleVerify','isHasGoogleSecretKey']),
            ...mapActions(['GetUserInfo','GetUserExten']),
            onClickLeft() {
                if (this.$route.query.goindex === 'true') {
                    this.$router.push('/');
                } else {
                    this.$router.back(-1);
                }
            },
            closes(){
                this.show = false
                this.message = ''
            },
            closeDialog() {
                // if (action === 'cancel') {//
                //     this.message = '';
                //     done();
                // } else if (action === 'confirm') {//
                //     if (this.message == '' || this.message == null || this.message == undefined) {
                //         this.$toast("");
                //         done(false)
                //     } else {
                //         this.action();
                //         this.show = false;
                //         done();
                //     }
                // }
                if (this.message == '' || this.message == null || this.message == undefined) {
                    this.$toast("");
                } else {
                    this.action();
                    this.show = false;
                }
            },
            goEditPsw() {
                this.pwdType = 1
                this.showPsw = true;
                this.iVerifyBusi = 3;
            },
            goEditDrawaPsw(){
                this.pwdType = 2
                this.drawal = {
                    sDrawalPassword: '',
                    sDpassword: ''
                }
                this.drawalShow = true;
                this.iVerifyBusi = this.iDrawalStatus() === 1 ?	19 :18;
            },
            goPayList(){
                if(this.iAdsStatus() !==2){
                    this.$message.warning('')
                    return
                }
                if(this.iDrawalStatus() !==1){
                  this.$message.warning('')
                  return
                }
                this.$router.push('/pay/list')
            },
            editPsw(action, done) {
                if (action === 'cancel') {//
                    done();
                    this.password = ''
                    this.newpassword = ''
                    this.repassword = ''
                } else if (action === 'confirm') {//
                    //dialog
                    done(false);
                    this.showPsw = true;
                    if (!this.password) {
                        this.$toast('');
                        return
                    }
                    if (!this.newpassword) {
                        this.$toast('');
                        return
                    }
                    if(!regPwd(this.newpassword)){
                        this.$toast('8-10');
                        return
                    }
                    if (!this.repassword) {
                        this.$toast('');
                        return
                    }
                    if (this.newpassword !== this.repassword) {
                        this.$toast('');
                        return
                    }
                    this.$refs.safeVerifyModal.visible = true
                }
            },
            drawalPsw(action, done){
                if (action === 'cancel') {//
                    done();
                    this.drawal = {
                        sDrawalPassword: '',
                        sDpassword: ''
                    }
                } else if (action === 'confirm') {//
                    //dialog
                    done(false);
                    if (!this.drawal.sDrawalPassword) {
                        this.$toast('');
                        return
                    }
                    if(!regPwd(this.drawal.sDrawalPassword)){
                        this.$toast('8-10');
                        return
                    }
                    if (!this.drawal.sDpassword) {
                        this.$toast('');
                        return
                    }
                    if (this.drawal.sDrawalPassword !== this.drawal.sDpassword) {
                        this.$toast('');
                        return
                    }
                    this.$refs.safeVerifyModal.visible = true
                }
            },
            setEmail(iVerifyBusi) {
                this.$refs.emailModal.iVerifyBusi = iVerifyBusi
                this.$refs.emailModal.visible = true
            },
            applyVerify(codes) {
                if(this.pwdType==1){
                    const params = {
                        iSource: 2,
                        sPassword: this.password,
                        sNewPassword: this.newpassword,
                    }
                    if (this.switchEmailVerify()) params.sEmailVerifyCode = codes.sEmailVerifyCode
                    if (this.switchCellphoneVerify()) params.sSMSVerifyCode = codes.sSMSVerifyCode
                    if (this.switchGoogleVerify()) params.sGoogleVerifyCode = codes.sGoogleVerifyCode
                    
                    changePassword(params)
                        .then(() => {
                            this.$toast('')
                            this.showPsw = false
                            this.GetUserInfo()
                            this.$refs.safeVerifyModal.visible = false
                        })
                }else{
                    const params ={
                        iSource:1,
                        sDrawalPassword:this.drawal.sDrawalPassword,
                    }
                    if (this.switchEmailVerify()) params.sEmailVerifyCode = codes.sEmailVerifyCode
                    if (this.switchCellphoneVerify()) params.sSMSVerifyCode = codes.sSMSVerifyCode
                    if (this.switchGoogleVerify()) params.sGoogleVerifyCode = codes.sGoogleVerifyCode
                    // 
                    resetDrawalPassword(params)
                    .then(()=>{
                        this.$message.success((this.iDrawalStatus() === 1 ? '' : '') +'')
                        this.drawalShow = false
                        this.$refs.safeVerifyModal.visible = false
                        this.GetUserExten()
                    })
                }
            },
            goAuthCode(){
                if(this.iIsChangeNameStatus()===0){
                    this.$toast('')
                    this.$router.push('/my/setting')
                    return
                }else if(!this.isHasEmail()){
                    this.$toast('')
                    return
                }
                this.goAuthShow = true
            },
            goAuth(action,index){
                this.$router.push({name:index==0?"authsetup":"authsetupf"});
            },
            goLiveAuth(){
                // if(this.email() ===''){
                //     this.$toast('')
                //     return
                // }else if(this.iCertificateStatus() != 2){
                //     this.$toast('')
                //     return
                // }else{
                //     this.$router.push({name:"authlive",query:{type: '2'}});
                // }
                this.$router.push({name:"authlive",query:{type: '2'}});
            },
            goAdsAuth(){
              if(this.iIsChangeNameStatus()===0){
                this.$toast('')
                this.$router.push('/my/setting')
                return
              }else if(!this.isHasEmail()){
                this.$toast('')
                return
              }
                // if(this.email() ===''){
                //     this.$toast('')
                //     return
                // }else if(this.iCertificateStatus() != 2){
                //     this.$toast('')
                //     return
                // }else if(this.iLiveCertificateStatus() != 2){
                //     this.$toast('')
                //     return
                // }else{
                //     this.$router.push({name:"authads",query:{type: '3'}});
                // }
              getAdsConfigList({iIdle: 0})
                  .then(res => {
                    // debugger
                    const tmp = res.data.vList.length > 0
                        ? Number(res.data.vList.find(i => i.iLevel === 1).iLowerDeposit)
                        : 0
                    this.$dialog.confirm({
                      title:'',
                      message: ' '+tmp+'USDT ',
                      messageAlign: 'left',
                      confirmButtonText:'',
                      confirmButtonColor: '#3D7EFF',
                      cancelButtonText:''
                    }).then(() =>{
                      this.$router.push({name:"authads",query:{type: '3'}});
                    })
                  })
            },
            goAuthSuccess(){
                this.$router.push({name:"authsetupsucess",query:{message: '',decs:''}});
            },
        }
    };

</script>

<style lang="less" scoped>
    .user-conts{
        //padding-top: 46px;
    }
    /deep/ .van-nav-bar{
        background: @gpay-bg3D7EFF;
        .van-nav-bar__text{
            color: @gpay-colorffffff;
        }
        .van-nav-bar__title{
            color: @gpay-colorffffff;
        }
    }
    /deep/.nk-icon .van-icon{
        font-size: .42rem;
        margin-top: -6px;
        margin-right: .06rem;
        .van-icon__image{
            height: auto;
        }
    }
    .-flat-button{
        height: auto !important;
        line-height: normal !important;
        min-width: auto !important;
    }
    .avatar-box{
        justify-content: center;
        align-items: center;
    }
    .popup-title-hed{
        font-size: .32rem;
        font-family: PingFang SC;
        font-weight: 400;
        color: #000000;
        margin: .32rem 0 .24rem .42rem;
    }
    .pass-style{
        color: seagreen;
        i{
            margin-top: 3px;
            font-size: 16px;
            position: relative;
            top: 4px;
            margin-right: 6px;
        }
    }
</style>
