import Vue from 'vue'
import { postAction } from './index'

const baseUrl = process.env.VUE_APP_GPAY_API_URL

const walletUrl = {
    GetWithdrawLimit:'/v1/udc/GetWithdrawLimit',                                                        //
    GetDepositAddress:'/v1/udc/GetDepositAddress',                                                      //
    UserWithdraw:'/v1/udc/UserWithdraw',                                                                //
    QueryWithdraw:'/v1/udc/QueryWithdraw',                                                              //
    GetMerchantTokens:'/v1/udc/GetMerchantTokens',                                                      //
    QueryDeposit:'/v1/udc/QueryDeposit',                                                                //
    EditWalletAddress:'/v1/udc/EditWalletAddress',                                                      //
    AddWalletAddress:'/v1/udc/AddWalletAddress',                                                        //
    DelWalletAddress:'/v1/udc/DelWalletAddress',                                                        //
    GetWalletAddress:'/v1/udc/GetWalletAddress',                                                        //
    QueryFinanceList:'/v1/udc/QueryFinanceList',                                                        //
}

const getWithdrawLimit = (params)=>postAction(walletUrl.GetWithdrawLimit,params);                       //
const getDepositAddress = (params)=>postAction(walletUrl.GetDepositAddress,params);                     //
const userWithdraw = (params)=>postAction(walletUrl.UserWithdraw,params);                               //
const queryWithdraw = (params)=>postAction(walletUrl.QueryWithdraw,params);                             //
const getMerchantTokens = (params)=>postAction(walletUrl.GetMerchantTokens,params);                     //
const queryDeposit = (params)=>postAction(walletUrl.QueryDeposit,params);                               //
const editWalletAddress = (params)=>postAction(walletUrl.EditWalletAddress,params);                     //
const addWalletAddress = (params)=>postAction(walletUrl.AddWalletAddress,params);                       //
const delWalletAddress = (params)=>postAction(walletUrl.DelWalletAddress,params);                       //
const getWalletAddress = (params)=>postAction(walletUrl.GetWalletAddress,params);                       //
const gueryFinanceList = (params)=>postAction(walletUrl.QueryFinanceList,params);                       //

for(let k in walletUrl) {
    walletUrl[k] = baseUrl + walletUrl[k]
}

export {
    walletUrl,
    getWithdrawLimit,
    getDepositAddress,
    userWithdraw,
    queryWithdraw,
    getMerchantTokens,
    queryDeposit,
    editWalletAddress,
    addWalletAddress,
    delWalletAddress,
    getWalletAddress,
    gueryFinanceList,
}